.App {
  text-align: center;
}
@import "./style/config";
html,
body {
  color: $text-color;
  font-family: "Alatsi", serif;
  font-size: 18px; // the "base size" = 1rem
  line-height: 140%;

  // resets
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  margin: 0 0 (3 * $vertical-space) 0;
}

p,
ul,
ol,
li {
  // vertical rhythm defined by bottom margins
  margin: 0 0 $vertical-space 0;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
